.main-navigation {
	clear: both;
   display: flex;
   align-items: center;
   flex-grow: 1;
	ul {
		display: none;
		list-style: none;
		margin: 0;
		padding-left: 0;

		ul {
			box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
			float: left;
			position: absolute;
			top: 100%;
			left: -999em;
			z-index: 99999;

			ul {
				left: -999em;
				top: 0;
			}

			li {
				&:hover > ul,
				&.focus > ul {
					left: 100%;
				}
			}

			a {
				width: 200px;
			}

			:hover > a,
			.focus > a {
			}

			a:hover,
			a.focus {
			}
		}

		li:hover > ul,
		li.focus > ul {
			left: auto;
		}
	}

	li {
		float: left;
		position: relative;

		&:hover > a,
		&.focus > a {
		}
	}

	a {
		display: block;
		text-decoration: none;
	}

	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a,
	.current-menu-ancestor > a {
	}
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

@media screen and (min-width: 37.5em) {
	.menu-toggle {
		display: none;
	}
	.main-navigation ul {
		display: block;
	}
}

.comment-navigation,
.posts-navigation,
.post-navigation {

	.site-main & {
		margin: 0 0 1.5em;
		overflow: hidden;
	}

	.nav-previous {
		float: left;
		width: 50%;
	}

	.nav-next {
		float: right;
		text-align: right;
		width: 50%;
	}
}

.site-branding {
   display: flex;
   flex-grow: 2;
}

.custom-logo-link {

}

.menu-menu-1-container {
   flex-grow: 1;
}

#primary-menu {
   display: flex;
   justify-content: space-around;
   
}

.menu-item a {
   padding: 10px;
   font-size: 12px;  
   transition: all 0.3s;
   text-align: center;
   color: $color__link;

   &:hover {
      color: $color__link-hover;
   }
}

.sub-menu {
   @include background-color;
   border-right: 1px solid;
   border-left: 1px solid;
   border-bottom: 1px solid;
   @include border-color(1);
   padding: 0;
   margin: 0;
}

